      line-height: 16px;\n        color: #212121;\n        background: #F5F5F5;\n        border-radius: 4px;\n        padding: 7px 10px;\n        margin-bottom: 12px;\n}\n.file-item[data-v-5398731a]:last-child {\n        margin-bottom: 0;\n}\n.file-name[data-v-5398731a] {\n        width: 300px;\n        white-space: nowrap;\n        overflow: hidden; \n        text-overflow: ellipsis\n}\n.remove-file[data-v-5398731a] {\n        font-size: 16px; \n        color: #757575; \n        justify-content: end; \n        display: flex; \n        margin-top: -16px;\n        cursor: pointer;\n}\n\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/giveUsFeedbackDialog.vue"],"names":[],"mappings":";AAgHI;QACI,uBAAuB;AAC3B;AACA;QACI,gBAAgB;AACpB;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,kBAAkB;AACtB;AACA;QACI,WAAW;QACX,eAAe;QACf,YAAY;QACZ,iBAAiB;AACrB;AACA;QACI,WAAW;QACX,aAAa;QACb,yBAAyB;QACzB,kBAAkB;QAClB,cAAc;QACd,YAAY;QACZ,kBAAkB;AAEtB;AACA;QACI,aAAa;AACjB;AACA;QACI,YAAY;QACZ,aAAa;QACb,UAAU;QACV,gBAAgB;QAChB,kBAAkB;QAClB,WAAW;AACf;AACA;QACI,WAAW;QACX,mBAAmB;QACnB,0BAA0B;QAC1B,kBAAkB;QAClB,eAAe;QACf,eAAe;AACnB;AACA;QACI,aAAa;QACb,oBAAoB;QACpB,gBAAgB;AACpB;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,kBAAkB;QAClB,cAAc;QACd,aAAa;QACb,mBAAmB;QACnB,iBAAiB;QACjB,kBAAkB;QAClB,iBAAiB;QACjB,YAAY;QACZ,uBAAuB;AAC3B;AACA;QACI,gBAAgB;QAChB,eAAe;QACf,kBAAkB;QAClB,cAAc;QACd,mBAAmB;QACnB,aAAa;QACb,mBAAmB;QACnB,iBAAiB;QACjB,kBAAkB;QAClB,YAAY;AAChB;AACA;QACI,eAAe;QACf,iBAAiB;QACjB,cAAc;QACd,mBAAmB;QACnB,kBAAkB;QAClB,iBAAiB;QACjB,mBAAmB;AACvB;AACA;QACI,gBAAgB;AACpB;AACA;QACI,YAAY;QACZ,mBAAmB;QACnB,gBAAgB;QAChB;AACJ;AACA;QACI,eAAe;QACf,cAAc;QACd,oBAAoB;QACpB,aAAa;QACb,iBAAiB;QACjB,eAAe;AACnB","sourcesContent":["<template>\n    <div class=\"container\">\n        <div class=\"label-div\">\n            <label class=\"type-label\">Feedback Type</label>\n            <CustomSelect :options=\"feedbackTypes\" @input=\"setType\" :selectedClass=\"'selected-type'\" :placeholder=\"'Select'\" class=\"select-type\"></CustomSelect>\n        </div>\n        <div class=\"label-div\">\n            <label class=\"type-label\">Description</label>\n            <textarea v-model=\"fbDescription\" class=\"description-text\" :placeholder=\"'What would you like to say to us?'\"></textarea>\n        </div>\n        <div class=\"label-div\">\n            <label class=\"type-label\">Images</label>\n            <input type=\"file\" name=\"attachment\" id=\"attachment\" class=\"hide-default-input\" @change=\"getFiles($event)\" accept=\"image/*\" multiple/>\n            <label for=\"attachment\" class=\"upload-files\">\n                <i class=\"mdi mdi-cloud-upload-outline\" style=\"font-size: 20px;color: #BCBCBC;margin-right: 8px;\"></i>\n                <span style=\"font-weight: 500\">Click</span> here to upload your image files\n            </label>\n        </div>\n        <div class=\"label-div\" style=\"overflow-y: auto; max-height: 156px;\">\n            <div class=\"file-item\" v-for=\"(file, index) in uploadedFiles\" :key=\"index\"> \n                <div class=\"file-name\">{{ file.name }}</div>\n                <i class=\"mdi mdi-close remove-file\" @click=\"removeFile(index)\"></i>\n            </div>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button id=\"sendButton\" class=\"submit-feedback-button\" @click=\"createFeedback()\">SUBMIT</button>\n        </div>\n    </div>\n</template>\n<script setup lang=\"ts\">\n    import { IFeedbackAttachment } from \"@stechquick/algae/lib/qCloudTemp/feedback\";\n    import { ref } from \"vue\";\n    import { container } from \"../../../../domain/core/diContainer\";\n    import { INotification } from \"../../../../domain/presentation/INotification\";\n    import { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\n    import { IUserUseCase } from \"../../../../domain/useCase/IUserUseCase\";\n    import { IViewModel } from \"../../../../domain/viewModel/IViewModel\";\n    import CustomSelect from \"../customSelect.vue\"\n\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const userUseCase = container.resolve<IUserUseCase>(IUserUseCase);\n    const notification = container.resolve<INotification>(INotification);\n\n    const feedbackTypes = [\"I have an idea\", \"I have a problem\", \"I have a question\",  \"Other\"];\n    const emit = defineEmits([\"close\"]);\n    var uploadedFiles = ref<Array<File>>([]);\n    var filesToSend = ref<Array<IFeedbackAttachment>>([]);\n    const fbType = ref<string>(\"\");\n    const fbDescription = ref<string>(\"\");\n    function close() {\n        emit(\"close\");\n    }\n    function setType (feedBacktype: string) {\n        fbType.value = feedBacktype;\n    }\n    function getFiles (event: Event) {\n        let files = (document.getElementById(\"attachment\")! as HTMLInputElement).files;\n        if(files!.length <= 3) {\n            for(let i = 0; i < files!.length; i++) {\n                if(files![i].size < 2097152) {\n                    if(uploadedFiles.value.length < 3) {\n                        uploadedFiles.value.push(files![i]);\n                        prepareFilesToSubmit(files![i]);\n                    }\n                }\n                else {\n                    event.preventDefault();\n                    notification.showNotification({type: \"warning\", text: \"File size cannot be bigger than 2MB\"})\n                }\n                \n            }\n        }\n        else {\n            event.preventDefault();\n            notification.showNotification({type: \"warning\", text: \"Maximum 3 files can be sent.\"})\n        }\n       \n\n        console.log(\"uploadedFiles => \", uploadedFiles.value);\n    }\n    function prepareFilesToSubmit (file: File) {\n        \n        let reader = new FileReader();\n        let test: IFeedbackAttachment;\n        test = {contentBase64: \"\", filename: \"\", contentType: \"\"};\n        reader.readAsDataURL(file); //convert to base64\n        reader.onload = function() {\n            test.contentBase64 = reader.result!.toString().split(',')[1];\n        }\n        test.filename = file.name;\n        test.contentType = file.type;\n        filesToSend.value.push(test);\n        \n        console.log(\"filesToSend => \", filesToSend.value);\n\n    }\n    const removeFile = (index: number) => {\n        uploadedFiles.value.splice(index, 1);\n        filesToSend.value.splice(index, 1); \n        return true;\n        \n    };\n    async function createFeedback() {\n        executor.execute(async () => {\n            await userUseCase.createFeedback({type: fbType.value, description: fbDescription.value, attachments: filesToSend.value});\n        }, { loading: true });\n        emit(\"close\");\n    }\n</script>\n\n<style scoped>\n    .container {\n        padding: 32px 20px 20px;\n    }\n    .label-div {\n        margin-top: 20px;\n    }\n    .type-label {\n        font-weight: 500;\n        font-size: 15px;\n        line-height: 18px;\n        color: #151233;\n        margin-bottom: 4px;\n    }\n    .select-type {\n        width: 100%;\n        font-size: 15px;\n        height: 40px;\n        line-height: 40px;\n    }\n    .description-text {\n        width: 100%;\n        height: 120px;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        color: #212121;\n        resize: none;\n        padding: 11px 12px;\n\n    }\n    textarea:focus {\n        outline: none;\n    }\n    .hide-default-input {\n        width: 0.1px;\n        height: 0.1px;\n        opacity: 0;\n        overflow: hidden;\n        position: absolute;\n        z-index: -1;\n    }\n    .upload-files{\n        width: 100%;\n        padding: 31px 110px;\n        border: 1px dashed #BCBCBC;\n        border-radius: 4px;\n        font-size: 15px;\n        cursor: pointer;\n    }\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 52px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .submit-feedback-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n    .file-item {\n        font-size: 14px;\n        line-height: 16px;\n        color: #212121;\n        background: #F5F5F5;\n        border-radius: 4px;\n        padding: 7px 10px;\n        margin-bottom: 12px;\n    }\n    .file-item:last-child {\n        margin-bottom: 0;\n    }\n    .file-name {\n        width: 300px;\n        white-space: nowrap;\n        overflow: hidden; \n        text-overflow: ellipsis\n    }\n    .remove-file {\n        font-size: 16px; \n        color: #757575; \n        justify-content: end; \n        display: flex; \n        margin-top: -16px;\n        cursor: pointer;\n    }\n\n</style>\n\n<style>\n    .selected-type {\n        background-color: transparent;\n        border-radius: 4px;\n        border: 1px solid #BCBCBC;\n        color: #212121;\n        padding-left: 12px;\n    }\n    .custom-placeholder:empty:before {\n        color:#BCBCBC !important;\n        font-weight: 400;\n    }\n    .custom-select .items .option {\n        font-size: 14px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n    /* body::-webkit-scrollbar-track {\n        background: #fafafa;\n    }\n\n    body::-webkit-scrollbar-thumb {\n        background: #BDBDBD;\n        border-radius: 8px;\n        border: 4px solid #FAFAFA;\n        background-clip: padding-box;\n    }\n    body::-webkit-scrollbar {\n        width:6px;\n    } */\n</style>"],"sourceRoot":""}]);
