<template>
  <div class="custom-select" :tabindex="tabindex" @blur="open = false">
    <div :class="open ? 'open selected ' + selectedClass : 'selected ' + selectedClass" @click="open = !open">
      <div v-if = "selected">{{ typeof selected === 'object' ? selected.name : selected }}
        <i class="mdi mdi-chevron-down"></i>
      </div>
      <div v-else>
        <span contenteditable="true" :data-placeholder="placeholder" class="custom-placeholder"></span>
        <i class="mdi mdi-chevron-down"></i>
      </div>
    </div>
    <div class="items" :class="{ selectHide: !open }" :style="{padding: extraButton ? '10px 0 0' : '10px 0'}">
      <div v-for="(option, i) of options" :key="i" @click="handleClick(option)" :class="['option' , (props.disabledOptions && getDisabledOptionsText(i)) ? 'disabled' : '' ]">
        <span v-if="props.disabledOptions && getDisabledOptionsText(i)" class="button-tooltip" :style="{top: (i*32) + 85 + 'px'}">{{getDisabledOptionsText(i)}}</span>
        <div>{{typeof option === 'object' ? option.name : option }}</div>
      </div>
      <div v-if="extraButton" class="button-div" :style="{borderTop: extraButton ? '1px solid #EEEEEE' :  'none'}" >
        <div v-for="(button, index) in buttons" :key="index">
            <div :id="'option-button-' + index" :class="button.buttonClass + ' button'" @click="open=false">{{ button.buttonText }}</div>
        </div>
      </div>
    </div>  
  </div>
</template>

<script setup>
import { ref, watchEffect } from 'vue'
const emit = defineEmits(["input"]);
const props = defineProps({
  options: {
    type: Array,
    required: true,
  },
  disabledOptions: {
    type: Array,
    required: false
  },
  default: {
    type: String,
    required: false,
    default: '',
  },
  selectedOption: {
    type: String,
    required: false
  },
  tabindex: {
    type: Number,
    required: false,
    default: 0,
  },
  placeholder: {
    type: String,
    required: false,
    default: "",
  },
  selectedClass: {
    type: String,
    required: true,
    default: "",
  },
  extraButton: {
    type: Boolean,
    required: false,
    default: false,
  },
  buttons: {
    type: Array,
    required: false,
    default: () => [],
    validator: (value) => {
      return value.every(
        (button) =>
          typeof button === "object" &&
          typeof button.buttonText === "string" &&
          typeof button.buttonClass === "string"
      );
    },
  },
})

const open = ref(false);
const selected = ref(props.default ? props.default : null);

watchEffect(() => {
  if(props && props.selectedOption){
    selected.value = props.selectedOption;
    open.value = false;
  }
});

const handleClick = (option) => {
    if(typeof option === 'object'){
      emit('input', option.id);
      selected.value = option;
      open.value = false;
      emit('input', selected.value.id);
    }
    else if(typeof option === 'string') {
      emit('input', option);
      selected.value = option;
      open.value = false;
      emit('input', selected.value);
    }
}

const getDisabledOptionsText = (index) => {
  const opt = props.disabledOptions.find(e => e.index == index);
  if(opt)
    return opt.cause;
  return undefined;
}
</script>

  
  <style scoped>
  /*You should put other CSS properties with same class names in the parent component's style tag that is not scoped.*/
  * {
    font-family: "Roboto"
  }
  .custom-select {
    position: relative;
    width: 100%;
    text-align: left;
    outline: none;
  }
  
  .custom-select .selected {

    cursor: pointer;
    user-select: none;
  }
  
  .custom-select .selected i {
    position: absolute;
    right: 10px;
    float: right;
  }
  
  .custom-select .items {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1;
  }
  
  .custom-select .items .option {
    cursor: pointer;
    user-select: none;
  }  
  
  .custom-select .items .option.disabled div {
    cursor: default;
    user-select: none;
    opacity: 0.4;
  } 
  .custom-select .items .option.disabled:active {
    pointer-events: none;
  }
  
  .selectHide {
    display: none;
  }
  .button-div {
    justify-content: center;
    display: grid;
    padding: 13px 0 19px;
    margin-top: 10px
  }
  .button {
    cursor: pointer;
  }
  .custom-placeholder:empty:before {
    content:attr(data-placeholder);
    color:#616161
  }
  .custom-placeholder:focus-visible {
    outline: none;
  }
  .button-tooltip {
    visibility: hidden;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 8px;
    top: 39px;
    left: 65px;
    height: 30px;
    position: absolute;
    background: #757575;
    color: #FFFFFF;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    border-radius: 4px;
    letter-spacing: 0.2px;
    opacity: 0;
    z-index: 1;
  }
  .custom-select .items .option:hover .button-tooltip {
    visibility: visible;
    transition: opacity 0.3s ease-out 0.5s;
    opacity: 1;
  }
  </style>